.PageWrapper{display:grid;place-items:center;margin-top:10px}.PageWrapper .nickname{color:var(--color-danger)}.PageWrapper_NotFound{display:grid;place-items:center;margin-top:10px;max-width:50%;text-align:center}.ResultWrapper{border-radius:5px;padding:10px;width:25%;line-height:2.225rem;border:1px solid #242830;margin-top:10px}@media screen and (max-width: 767px){.ResultWrapper{width:90%}}.ProfileLine{margin:1rem 0;color:inherit;border:0;border-top:1px solid;opacity:.25;border-style:double;border-color:#fff}.h5{margin-bottom:.5rem;font-weight:500;line-height:1.2;font-size:1.25rem}.ButtonGroup{display:flex;margin-top:15px;flex-direction:row}.ButtonGroup button{margin-right:10px}.ButtonGroup button:last-of-type{margin-right:0}.Table{border:1px solid rgba(73,80,87,.4196078431);padding:1.5rem;border-radius:.375rem;width:100%;text-align:center;margin-top:10px}@media screen and (max-width: 767px){.Table{width:90%}}.Table>thead{vertical-align:bottom}.Table tbody,.Table td,.Table tfoot,.Table th,.Table thead,.Table tr{border-color:inherit;border-style:solid;border-width:0}.Table tbody a,.Table td a,.Table tfoot a,.Table th a,.Table thead a,.Table tr a{background:rgba(0,0,0,0);appearance:none}.Table>:not(caption)>*>*{padding:.5rem .5rem;color:initial,initial,#fff;border-bottom-width:1px;box-shadow:inset 0 0 0 9999px initial,initial,rgba(0,0,0,0)}.Table>tbody{vertical-align:inherit}.Table>tbody tr{border-color:inherit;border-style:solid;border-width:0}