.button{display:inline-flex;align-items:center;justify-content:center;padding:12px 20px;border-radius:4px;transition:.3s ease;color:#fff;font-size:16px;width:100%;cursor:pointer;user-select:none}.button svg{margin-right:6px;margin-bottom:2px;font-size:20px}.button:disabled{cursor:not-allowed;opacity:.5}.Primary{background:#1339ff;border:1px solid rgba(0,0,0,0)}.Primary:enabled:hover{background:rgb(54.7,86.9516949153,255)}.Transparent{background:rgba(0,0,0,0);border:1px solid #1339ff}.Transparent:enabled:hover{border-color:rgb(54.7,86.9516949153,255)}.Secondary{border:1px solid #191b1d;background:rgba(0,0,0,0)}.Secondary:enabled:hover{background:#202325;border:1px solid #191b1d}.Danger{border:1px solid rgba(0,0,0,0);background:#ff6955}.Danger:enabled:hover{background:rgb(255,136.5,120.7)}